import * as React from 'react';

import CanvasControlType from './CanvasControlType';
import ISerializedCanvasControl from './ISerializedCanvasControl';
import SPRte from '../../sp-rte/rte/SPRte';
import WebPartZone from '../sampleCanvasControls/WebPartZone';

export default class CanvasControlFactory {
  public static CreateCanvasControl(type: CanvasControlType, defaultProps: ISerializedCanvasControl): JSX.Element {
    let control: JSX.Element;

    switch (type) {
      case CanvasControlType.RTE:
        control = React.createElement(SPRte, defaultProps);
        break;
      case CanvasControlType.WebPartZone:
        control = React.createElement(WebPartZone, defaultProps);
        break;
    }

    return control;
  }
}
